import Link from 'next/link';
import React from 'react';
import { BagConsumer } from '../components/BagProvider';
import styled from 'styled-components';

export default class Footer extends React.Component {
  render() {
    const linkStyle = {
      marginRight: 15,
    };

    const Wrapper = styled.section`
      padding: 1em;
      background-color: #fdfffc;
    `;

    const NavAnchor = styled.a`
      color: #131513;
      &:hover {
        color: #ff9f1c;
        cursor: pointer;
      }
      &:visited {
        color: #131513;
        cursor: pointer;
      }
    `;

    return (
      <BagConsumer>
        {({ bag }) => (
          <Wrapper className="footer">
            <Link href="/">
              <NavAnchor style={linkStyle}>Home</NavAnchor>
            </Link>

            <Link href="/products">
              <NavAnchor style={linkStyle}>Products</NavAnchor>
            </Link>

            <Link href="/about">
              <NavAnchor style={linkStyle}>About</NavAnchor>
            </Link>

            <Link href="/bag">
              <NavAnchor style={linkStyle}>Bag ({bag.length})</NavAnchor>
            </Link>
          </Wrapper>
        )}
      </BagConsumer>
    );
  }
}
