import React, { Component } from 'react'
import products from '../data/products.js'
import * as _ from 'lodash';
import Router from 'next/router'

const SearchContext = React.createContext()

/* Then create a provider Component */
class SearchProvider extends Component {
    constructor(props) {
        super(props);

        this.handleSearchTermChange = this.handleSearchTermChange.bind(this);
        this.findProductBasedOnSearch = this.findProductBasedOnSearch.bind(this);
        this.deleteSearchResults = this.deleteSearchResults.bind(this);

        this.state = {
          searchTerm: '',
          searchResults: [],
          displayedSearchTerm: '',
          displaySearchResults: false
      };
    }

    handleSearchTermChange = (e) => {
        e.preventDefault();
        var searchTerm = e.currentTarget.value;
        this.setState({ searchTerm: searchTerm });
    }

    findProductBasedOnSearch(){  
        let filteredProducts =_.filter(products, { tags: [{ name: this.state.searchTerm.toLocaleLowerCase() }]});
        this.setState({searchResults: filteredProducts, displayedSearchTerm: this.state.searchTerm, displaySearchResults: true});
        Router.push('/products');
    }
    
    deleteSearchResults(){
        this.setState({searchResults: [], displaySearchResults: false});
    }

    render () {
        return (
        <SearchContext.Provider
            value={{
                searchTerm: this.state.searchTerm,
                searchResults: this.state.searchResults,
                handleSearchTermChange: this.handleSearchTermChange,
                findProductBasedOnSearch: this.findProductBasedOnSearch,
                deleteSearchResults: this.deleteSearchResults,
                displayedSearchTerm: this.state.displayedSearchTerm,
                displaySearchResults: this.state.displaySearchResults
            }}>
        
            {this.props.children}
        </SearchContext.Provider>
        )
    }
}

const SearchConsumer = SearchContext.Consumer

export default SearchProvider
export { SearchConsumer }