import Link from 'next/link';
import React from 'react';
import { BagConsumer } from '../components/BagProvider'
import SearchComponent from '../components/Search'
import styled, { keyframes } from 'styled-components';

export default class Header extends React.Component {


  render() {
    const linkStyle = {
      marginRight: 15
    };

    const Wrapper = styled.nav`
      padding: 1em;
      background-color: #011627;
      color: #fdfffc;
      position: fixed;
      width: 100%;
      z-index: 1;
      top: 0;
      display: flex;
      align-content: center;
      justify-content: center;
      flex-direction: row;

    `;

    const NavAnchor = styled.a`
      color: #fdfffc;
      &:hover {
        color: #FF9F1C;
        cursor: pointer;
      }
      &:visited{
        color: #FF9F1C;
        cursor: pointer;
      }
    `

    const LogoAnchor = styled.a`
       color: #FF9F1C;
       font-size: 1.9rem;
      &:hover {
        color: #fdfffc;
        cursor: pointer;
      }
      &:visited{
        color: #fdfffc;
        cursor: pointer;
      }
      flex: 1 0 0;
    `

    const SiteLinkContainer = styled.div`
      flex: 1 0 0;
      align-self: center;
      position: relative;
    `

    const rotate = keyframes`
    from {
      transform: translate3d(0, 0, 0);
    }

    to {
      transform: translate3d(0, 30px, 0)
    }`;

    
    const AddToBagAnimation = styled.div`
      display:  ${props => props.inputDisplay || "none"};
      animation: ${rotate} 0.5s;
      animation-direction: alternate;
      animation-timing-function: cubic-bezier(.5,0.05,1,.5);
      animation-iteration-count: infinite;
      position: absolute;
      left: 176px;
      top: 22px;

    `;

    const HeaderAlertflag = styled.button`
        color: #fdfffc;
        background-color: #e71d36;
        border-color: #e71d36; 
        font-size: 0.6rem;
        padding: 0px 5px;
        font-weight: 900;
        height: 24px;
        margin: 0;
        line-height: 0;
        :after {
          content:'';
            position: absolute;
            left: 103px;
            right: 0;
            bottom: 24px;
            margin: 0 auto;
            width: 0;
            height: 0;
            border-bottom: 11px solid #e71d36;
            border-left: 10px solid transparent;
            border-right: 10px solid transparent;
        }
    `;

    return (
      <BagConsumer>
        {({ bag, playBagAnimation }) => (

          <Wrapper className="header">
           
            <Link href="/">
              <LogoAnchor style={linkStyle}>Tennie Bee</LogoAnchor>
            </Link>

            <SearchComponent/>
            <SiteLinkContainer>
              <Link href="/about">
                <NavAnchor style={linkStyle}>About</NavAnchor>
              </Link>

              <Link href="/products">
                <NavAnchor style={linkStyle}>Products</NavAnchor>
              </Link>

              <Link href="/blog">
                <NavAnchor style={linkStyle}>Blog</NavAnchor>
              </Link>

              <Link href="/contact">
                <NavAnchor style={linkStyle}>Contact</NavAnchor>
              </Link>

              <Link href="/bag">
                <NavAnchor style={linkStyle}>Bag ({bag.length})</NavAnchor>
              </Link>

              <AddToBagAnimation inputDisplay={playBagAnimation ? 'display-block' : false}>
                <HeaderAlertflag className="button">New item in bag</HeaderAlertflag>
              </AddToBagAnimation>
            </SiteLinkContainer>

          </Wrapper>
        )}
      </BagConsumer>
    )
  }
}