import React, { Component } from 'react'
import * as _ from 'lodash';
import 'es6-promise';
import fetch from 'isomorphic-fetch';

/* First we will make a new context */
const BagContext = React.createContext()

/* Then create a provider Component */
class BagProvider extends Component {
    constructor(props) {
        super(props);

        this.handleBagUpdate = this.handleBagUpdate.bind(this);
        this.handleVariantChange = this.handleVariantChange.bind(this);
        this.handleDeleteItem = this.handleDeleteItem.bind(this);
        this.handleQuantityChange = this.handleQuantityChange.bind(this);
        this.clearBag = this.clearBag.bind(this);
        this.displayBagTotals = this.displayBagTotals.bind(this);
        this.handleNotesUpdate = this.handleNotesUpdate.bind(this);
        this.handleHelpChange = this.handleHelpChange.bind(this);
        this.handleMetadataChange = this.handleMetadataChange.bind(this);
        this.logOrder = this.logOrder.bind(this);

        this.state = {
          bag: [],
          selectedVariant: {
            id: 0,
            sku: '',
            price: '',
            quantity: 0,
            image:'',
            name: '',
            useNotes: false,
            notesTitle: '',
            noteContents: '',
            hasNotesError: false,
            metadata: []
          },
          playBagAnimation: false,
          showHelp: false
      };
    }

    componentDidMount() {
        var savedBag = sessionStorage.craftyBeeBag ? JSON.parse(sessionStorage.craftyBeeBag) : [];
        this.setState({ bag: savedBag });
    }

    componentDidUpdate() {
        sessionStorage.setItem('craftyBeeBag', JSON.stringify(this.state.bag));
    }

    handleBagUpdate = (e) => {
        e.preventDefault();
        var bag = sessionStorage.craftyBeeBag ? JSON.parse(sessionStorage.craftyBeeBag) : this.state.bag;
        var selectedVariant = this.state.selectedVariant;
        if(selectedVariant.useNotes && selectedVariant.noteContents === ''){
            selectedVariant.hasNotesError = true;
            this.setState({ selectedVariant: selectedVariant });
            return false;

        }
        else if(selectedVariant.hasNotesError === true && selectedVariant.useNotes && selectedVariant.noteContents !== ''){
            selectedVariant.hasNotesError = false;
            this.setState({ selectedVariant: selectedVariant });
        }

        let bagItem = _.find(bag, { sku: selectedVariant.sku});
        let index = _.findIndex(bag, {sku: selectedVariant.sku});

        if(bag.length > 0 && index != -1){
            var newQuantity = bagItem.quantity + 1;
            bag.splice(index, 1, {sku: selectedVariant.sku, quantity: newQuantity, 
                                  image: selectedVariant.imageTiny, name: selectedVariant.name, 
                                  price: selectedVariant.price, noteContents: selectedVariant.noteContents, metadata: selectedVariant.metadata});
        }
        else{
            bag.push({sku: selectedVariant.sku, quantity: 1, 
                        image: selectedVariant.imageTiny, 
                        name: selectedVariant.name, 
                        price: selectedVariant.price,                   
                        noteContents: selectedVariant.noteContents,
                        metadata: selectedVariant.metadata
                     });
        }
    
        this.setState({ bag: bag, playBagAnimation: true });

        setTimeout(
            function() {
                this.setState({ playBagAnimation: false });
            }
            .bind(this),
            3000
        );
    }

    handleVariantChange(e){
        var id = Number(e.currentTarget.value);
        let selectedOption = null;
        let metadata = null;
        for(var i = 0; i < this.props.products.length; i++){
            selectedOption = _.find(this.props.products[i].options, ['id', id]);
            metadata = this.props.products[i].metadata;
            if(selectedOption){
                break;
            }
        }
        
        var selectedVariant = this.state.selectedVariant;
        selectedVariant.id = selectedOption.id;
        selectedVariant.sku = selectedOption.sku;
        selectedVariant.price = selectedOption.price;
        selectedVariant.imageTiny = selectedOption.imageTiny;
        selectedVariant.imageLarge = selectedOption.imageLarge;
        selectedVariant.name = selectedOption.name;
        selectedVariant.useNotes = selectedOption.useNotes;
        selectedVariant.notesTitle = selectedOption.notesTitle;
        selectedVariant.noteContents = '';
        
        //product metadata gets attached to selected variant(makes things sort of messy)
        selectedVariant.metadata = []

        if(metadata && metadata.length > 0){
            selectedVariant.metadata.push({name: metadata[0].groupName, value:metadata[0].name});
        }
       
        this.setState({ selectedVariant: selectedVariant, showHelp: false });
    }

    handleDeleteItem = sku => {
        const bag = this.state.bag.filter(item => item.sku !== sku);
        this.setState({ bag: bag });
    
    };

    handleQuantityChange(e, sku){
        e.preventDefault();

        let bag = this.state.bag;
        var quantity = Number(e.target.value);
        let bagItem = _.find(bag, { sku: sku});
        let index = _.findIndex(bag, {sku: sku});

        bag.splice(index, 1, {sku: bagItem.sku, quantity: quantity, 
                              image: bagItem.image, name: bagItem.name, 
                              price: bagItem.price, useNotes: bagItem.useNotes, 
                              notesTitle: bagItem.notesTitle, noteContents: bagItem.noteContents,
                              metadata: bagItem.metadata});
        this.setState({ bag: bag });  
    }

    handleNotesUpdate(e, id){
        e.preventDefault();
        let noteText = e.target.value;
        var selectedVariant = this.state.selectedVariant;
        selectedVariant.noteContents = noteText;
        this.setState({ selectedVariant: selectedVariant });

    }

    handleHelpChange(e){
        this.setState({showHelp: true});
    }

    handleMetadataChange(e){
        let value = e.currentTarget.value;
        let name = e.target.item(e.target.selectedIndex).getAttribute('name');
        var selectedVariant = this.state.selectedVariant;

        for(var i = 0; i < selectedVariant.metadata.length; i++){
            if(selectedVariant.metadata[i].name === name){
                selectedVariant.metadata.splice(i, 1, {name: selectedVariant.metadata[i].name, value: value});
                this.setState({selectedVariant: selectedVariant});
                return;
            }
        }

        selectedVariant.metadata.push({name: name, value: value});
        this.setState({selectedVariant: selectedVariant});
    }

    formatDollarsToCents(value){
        value = (value + '').replace(/[^\d.-]/g, '');
        if (value && value.includes('.')) {
            value = value.substring(0, value.indexOf('.') + 3);
        }
    
        return value ? Math.round(parseFloat(value) * 100) : 0;
    }

    formatCentsToDollars(value){
        value = (value + '').replace(/[^\d.-]/g, '');
        value = parseFloat(value);
        return value ? value / 100 : 0;
    }

    clearBag(){
        this.setState({ bag: [] });
    }

    displayBagTotals(){
        let totals = {
            subtotal: 0.00,
            shipping: 0.00,
            tax: 0.00,
            grandTotal: 0.00
        }

        let subtotal = 0;
        let bag = this.state.bag;
        for(var i = 0; i < bag.length; i++){
            var subTotalCalculation;
            subTotalCalculation = this.formatDollarsToCents(bag[i].price);
            subTotalCalculation = (subTotalCalculation * bag[i].quantity);
            subtotal += subTotalCalculation;
        }
        
        let tax = subtotal * 6.0;

        let shipping = 0;
        let grandTotal = subtotal + Math.round(tax / 100);

        totals.subtotal = this.formatCentsToDollars(subtotal);
        totals.tax = this.formatCentsToDollars(Math.round(tax / 100));
        totals.grandTotal = this.formatCentsToDollars(grandTotal);

        return totals;
    }

    logOrder(){

        let bag = this.state.bag;
        for(var i = 0; i < bag.length; i++){
            let metaData = '';
            if(bag[i].metadata && bag[i].metadata.length > 0){
                for(var j = 0; j < bag[i].metadata.length; j++){
                    metaData += bag[i].metadata[j].name + ' | ' + bag[i].metadata[j].value;
                }
            }
            console.log(metaData)
            
            let headers = new Headers({
                'Access-Control-Allow-Origin':'*',
                'Content-Type': 'application/json',
                }); 

            fetch('https://3ee.com/teeniebee/orders/', {
                method: 'post',
                mode: 'cors',
                headers: headers,
                body: JSON.stringify({
                    title: bag[i].name,
                    sku: bag[i].sku,
                    quantity: bag[i].quantity,
                    price: bag[i].price,
                    note: bag[i].noteContents,
                    metadata: metaData
                })
            })
            .then((res) => {
                console.log(res);
            })
        }
        //log items
        

        /* let header = new Headers({
        'Access-Control-Allow-Origin':'*',
        'Content-Type': 'multipart/form-data'
        });  */
    }

    render () {
        return (
        <BagContext.Provider
            value={{
                bag: this.state.bag,
                selectedVariant: this.state.selectedVariant,
                handleBagUpdate: this.handleBagUpdate,
                handleVariantChange: this.handleVariantChange,
                handleDeleteItem: this.handleDeleteItem,
                handleQuantityChange: this.handleQuantityChange,
                clearBag: this.clearBag,
                displayBagTotals: this.displayBagTotals,
                playBagAnimation: this.state.playBagAnimation,
                handleNotesUpdate: this.handleNotesUpdate,
                showHelp: this.state.showHelp,
                handleHelpChange: this.handleHelpChange,
                handleMetadataChange: this.handleMetadataChange,
                logOrder: this.logOrder
            }}>
        
            {this.props.children}
        </BagContext.Provider>
        )
    }
}

/* then make a consumer which will surface it */
const BagConsumer = BagContext.Consumer

export default BagProvider
export { BagConsumer }