import React from 'react';
import * as _ from 'lodash';
import styled from 'styled-components'
import { SearchConsumer } from '../components/SearchProvider'

const SearchContainer = styled.div`
 display: inline-block;
 flex: 1 0 28%;
`

const SearchBox = styled.input`
 color: rgb(1, 22, 39);
 width: 65%;
 border-radius: 0 0 0 4px !important;
`

const SearchButton = styled.button`
    border-radius: 0px 4px 4px 0px !important;
`

class Search extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <SearchConsumer>
             {({ handleSearchTermChange, findProductBasedOnSearch }) => (
                <SearchContainer>
                    <SearchBox
                        type="text"
                        onChange={e => handleSearchTermChange(e)}
                        placeholder="What are you looking for?"
                    />
                    <SearchButton onClick={e => findProductBasedOnSearch(e)} className="button button-secondary">Search</SearchButton>

                </SearchContainer>
                )}
           </SearchConsumer>
        );
    }
}

export default Search;