import React, { Component } from 'react'
import Layout from '../components/Layout.js';
import Head from 'next/head'

export default class index extends Component {
  render() {
    return (

      <Layout>
        <div>
          <Head>
            <title>Crafty Bee Supplies</title>
            <meta
              name="viewport"
              content="initial-scale=1.0, width=device-width"
              key="viewport"
            />
          </Head>

        </div>
        <div className="section hero">
          <div className="container">
            <div className="row">
              <div className="one-half column">
                <h4 className="hero-heading">Latest Disney Theme products available now. Let users view sites from anywhere.</h4>
                <a className="button button-secondary" href="/products">Shop Now</a>
              </div>
            </div>
          </div>
        </div>
        <hr />
        <div className="container">
          <div className="row">
            <div className="four columns">
              <h4>Fall Fashion</h4>
              <p>ge tthe latest in fall fasion right at your door with all things free shipping</p>
              <a className="button button-primary" href="/products">Shop Now</a>
            </div>
            <div className="four columns">
              <h4>Disney Supplies</h4>
              <p>Craft together your Disney themed products for own products</p>
              <a className="button button-primary" href="/products">Shop Now</a>
            </div>
            <div className="four columns">
              <h4>New Materials</h4>
              <p>All the latest materials with our free shipping bonzzania</p>
              <a className="button button-primary" href="/products">Shop Now</a>
            </div>

          </div>

        </div>

      </Layout>
    );
  }
}